	module.exports = {"data":{"site":{"siteMetadata":{"title":"Gatsby Starter - Forty by HTML5 UP","author":"Hunter Chang"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/there-be-pixels/index.md absPath of file >>> MarkdownRemark","html":"<p><object width=\"500\" height=\"300\" data=\"http://tv.adobe.com/Embed.swf\" type=\"application/x-shockwave-flash\"><param name=\"name\" value=\"AdobeTVPlayer\" /><param name=\"bgcolor\" value=\"#000000\" /><param name=\"flashvars\" value=\"v=~b64~aHR0cDovL2Fkb2JlLmVkZ2Vib3NzLm5ldC9mbGFzaC9hZG9iZS9hZG9iZXR2Mi9tYXhfMjAwOF9kZXZlbG9wLzE1OTY3NDE2MTNfMjg3NTg0MDAwMV8yMDAyLWtsaW5nZW1hbm4td2VkLTMzMHBtLWRlc2lnbi1kZXZlbG9wLmZsdj9yc3NfZmVlZGlkPTE1Mzg0JnhtbHZlcnM9Mg==&amp;w=500&amp;t=http://tv.adobe.com/#vi+f15384v1012&amp;h=300\" /><param name=\"src\" value=\"http://tv.adobe.com/Embed.swf\" /><param name=\"quality\" value=\"high\" /></object></p>\n<p><a href=\"http://tv.adobe.com/#vi+f15384v1012\" target=\"_blank\">\"Here Be Pixels\" by Mario Klingemann</a> is an interesting presentation from MAX 2008.</p>\n<p>In it, Mario talks about using bitmaps and bitmapdata with cameras to do everything from a barcode reader to augmented reality.  He doesn't cover the code, but does cover the theory and shows some implimentations, and references research that this was all built from.</p>\n<p>I feel like I've just sat through an intensive lecture at university all over again - I did a motion capture project with much of the same technology back at university, so I found it very interesting to see just how far the same technologies could be taken.</p>\n<p>Have a watch, if you have 56 minutes!</p>","frontmatter":{"title":"There Be Pixels!","date":"January 12, 2009","tags":["bitmap","bitmapdata","pixels"]}}},"pathContext":{}}

